a {
	text-decoration: none;
}
#header {
	position: absolute;
	left: 0;
	top: 0;
	z-index: 1000;
	width: 100%;
	height: 117px;
	overflow: hidden;
	text-align: center;
	background-color: #fff;
	box-shadow: 0 0 2px rgba(0,0,0,0.3);
	transition: height 0.3s;
}
#header:hover {
	height: 380px;
}
#header::after {
	content: "";
	display: block;
	position: absolute;
	left: 0;
	top: 117px;
	width: 100%;
	height: 1px;
	background-color: #eaeaea;
}
#header .inner {
	position: relative;
	margin: 0 auto;
	width: 1200px;
}
#header .inner .logo{
	position: absolute;
	left: 0;
	top: 0px;
}
#header .inner .logo a{
	display: block;
	line-height: 117px;
	margin-top: 10px;
}
#header .inner .logo a img {
	max-width: 100%;
}
#header .inner .utils {
	position: absolute;
	right: 0;
	top: 35px;
}
#header .inner .utils .left{
	float: left;
}
#header .inner .utils .left li{
	display: inline-block;
}
#header .inner .utils .left li a{
	display: block;
	padding: 6px;
	font-size: 14px;
	position: relative;
}
#header .inner .utils .left li:first-child a::after {
	content: "";
	right: 0px;
	top: 10px;
	position: absolute;
	display: block;
	width: 2px;
	height: 11px; 
	background-color: #8f8f8f;
}
#header .inner .utils .right{
	float: right;
	margin-left: 5px;
	padding: 5px;
}
#header .inner .utils .right select{
	border: none;
	margin-right: 5px;
}
#header .inner .utils .right a {
	float: right;
	width: 23px;	
}
#header .inner .utils .right a img{
	max-width: 100%;
}
#header .inner .nav {
	position: relative;
}
#header .inner .nav .gnb_left {
	position: absolute;
	left: 0;
	top: calc(118px + (380px - 118px - 180px)/2 - 10px);
	width: 144px;
	height: 180px;
	text-align: center;
}
#header .inner .nav .gnb_left img{
	display: inline-block;
	height: 180px;
}
#header .inner .nav .gnb_right {
	position: absolute;
	right: 0;
	top: calc(118px + (380px - 118px - 180px)/2 - 10px);
	width: 125px;
	height: 205px;
	overflow-x: hidden;
}
#header .inner .nav .gnb_right .slidelist{
	position: absolute;
	width: calc(3*125px);
	height: 180px;
	top: 0;
	left: 0;
}
#header .inner .nav .gnb_right .slide{
	float: left;
}
#header .inner .nav .gnb_right .slide img{
	width: 125px;
	height: 180px;
}
#header .inner .nav .gnb_right .pagination{
	width: 100%;
	text-align: center;
	margin-top: 180px;
}
#header .inner .nav .gnb_right .pagination li{
	display: inline-block;
    width: 6px;
    height: 6px;
    background-color: #fff;
    border-radius: 50%;
}
#header .inner .nav .gnb_right .pagination li.active{
	background-color: #f90;
	opacity: 1;
}
#gnb > ul {
	margin-top: 5px;
}
#gnb > ul > li {
	display: inline-block;
	position: relative;
}
#gnb > ul > li > a {
	display: block;
	position: relative;
	padding: 10px 36px 22px;
	line-height: 60px;
	font-size: 18px;
	font-weight: 800;
	color: #333;
}
#gnb li:nth-child(2) ul, 
#gnb li:nth-child(4) ul {
	width: 140%;
}
#gnb > ul > li > a > span {
	display: inline-block; 
	position: relative;
	padding: 10px 0;
}
#gnb > ul > li > a > span::after {
	content: "";
	display: block;
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	height: 3px;
	background-color: #f90;
	-webkit-transform: scaleX(0);
	        transform: scaleX(0);
	transition: -webkit-transform 0.3s;
	transition: transform 0.3s;
	transition: transform 0.3s, -webkit-transform 0.3s;
}
#gnb > ul > li:hover > a > span::after {
	-webkit-transform: scaleX(1);
	        transform: scaleX(1);
}
#gnb li ul {
	position: absolute;
	left: 25px;
	margin: 20px 0;
	width: 100%;
	height: 220px;
	border-left: 1px solid #eaeaea;
}
#gnb li ul a {
	display: block;
	position: relative;
	padding: 0 35px 0 0;
	line-height: 38px;
	text-align: left;
	text-indent: 15px;
	font-size: 15px;
	color: #666;
}
#gnb li ul a:hover {
	color: #f90;
}
#gnb li ul a::before {
	content: "";
	display: none;
	position: absolute;
	left: -3px;
	top: 17px;
	width: 6px;
	height: 6px;
	background-color: #f90;
	border-radius: 3px;
}
#gnb li ul a:hover::before {
	display: block;
}
