.main_slider .pagebtn li {
	position: absolute;
	width: 43px;
	height: 32px;
	border-bottom: 1px solid #000;
}
.main_slider .pagebtn li a{
	display: block;
}
.main_slider .pagebtn .next{
	display: block;
	right: 0;
	top: 50%;
	-webkit-transform: translateY(-50);
	        transform: translateY(-50);
	padding-right: 50px;
}
.main_slider .pagebtn .next a{
    display: block;
    line-height: 32px;
    text-transform: uppercase;
    font-size: 17px;
    font-weight: 400;
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
}
.main_slider .pagebtn .next:hover a{
	-webkit-transform: translateX(40px);
	        transform: translateX(40px);
}
.main_slider .pagebtn .prev{
	display: block;
	left: 0;
	top: 50%;
	padding-left: 50px;
	-webkit-transform: translateY(-50);
	        transform: translateY(-50);
}
.main_slider .pagebtn .prev a{
    display: block;
    line-height: 32px;
    text-transform: uppercase;
    font-size: 17px;
    font-weight: 400;
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
}
.main_slider .pagebtn .prev:hover a{
	-webkit-transform: translateX(-40px);
	        transform: translateX(-40px);
}
