#footer .footer_center .inner .util li{
	display: inline-block;
}
#footer .footer_center .inner .util li a{
	display: block;
	color: #D9D9D9;
	line-height: 18px;
	padding: 9px;
	font-size: 15px;
	position: relative;
}
#footer .footer_center .inner .util li a::after{
	content: "";
	display: block;
	position: absolute;
	right: -3px;
	top: 11px;
	width: 3px;
	height: 14px;
	background-color: #666;
}
#footer .footer_center .inner .util li:last-child a::after{ 
	display: none;
}
#footer .footer_center .inner .right {
	float: right;
	padding: 20px 0;
}
#footer .footer_center .inner .right .sns {
	display: inline-block;
}
#footer .footer_center .inner .right .sns li{
	display: inline-block;
	vertical-align: middle;
	padding-left: 4px;
}
#footer .footer_center .inner .right .sns li a{
	display: block;
	background-color: #444;
	border-radius: 50%;
	width: 39px;
	height: 39px;
	text-align: center;
}
#footer .footer_center .inner .right .sns li a img{
	width: 50%;
	padding-top: 10px;	
	padding-left: 2px;
}
#footer .footer_center .inner .right .english { 
	display: inline-block;
	vertical-align: middle;
	background-color: #444444;
	margin-left: 10px;
}
#footer .footer_center .inner .right .english a{
	display: block;
	width: 214px;
	height: 44px;
	position: relative;
}
#footer .footer_center .inner .right .english a span{
	padding-left: 12px;
	line-height: 44px;
	color: #D9D9D9;
	font-size: 14px;
	font-weight: 200;
}
#footer .footer_center .inner .right .english a::after{
    content: '';
    position: absolute;
    right: 1em;
    top: 50%;
    width: 0;
    height: 0;
    margin-top: -4px;
    border-width: 7px 6px 0 6px;
    border-color: #ff8200 transparent transparent transparent;
    border-style: solid;
}
